import React, { useContext, useState } from "react";
import { UserContext } from "../../providers/UserProvider";
import { pemFirestore } from "../../firebase/config";
import UploadForm from "./UploadForm";
import useNewScenarioHtml from "./useNewScenarioHtml";

export default function NewScenario({ dceId, dceFeatures, dceDetails, scNr, setScNr, chosen, setChosen, handleAddScenario, setIsSaved, isSaved}) {

    const user = useContext(UserContext);
    const uid = user.uid;
    const [newScNr, setNewScNr] = useState(scNr);
    const disabled = 'true';


    //Handles choices from dropdown fValues
    const handleChosen = (value, name)=>{
        console.log('value and name in handleChosen: '+value+name)
        // e.preventDefault();
        // const {name, value} = e.target;
        const chosenList = [...chosen];
        console.log('chosenList in handleChosen: '+JSON.stringify(chosenList))
        chosenList[0]["data"]["fValues"][name]= value;
        setChosen(chosenList);
        setIsSaved(false);
    }

    //Handles entries from input fields
    const handleEntry = (e)=>{
        e.preventDefault();
        const {name, value} = e.target;
        const chosenList = [...chosen];
        chosenList[0]["data"][name]= value;
        setChosen(chosenList);
        setIsSaved(false);
    }

//Gets features for the other scenario in a pair
const getAltFValues = (fValueList) =>{
    const groupFeat = dceDetails.details.groupFeature;
    const groupFeatValue = fValueList[groupFeat];
    let newFeatValue;
    dceFeatures.map((feature,i) =>{
        if (feature.featureName===groupFeat){
            if(feature.featureValue1===groupFeatValue){
                newFeatValue = feature.featureValue2;}
            if(feature.featureValue2===groupFeatValue){
                newFeatValue = feature.featureValue1;}
        }
    })
    let altFValueList = {...fValueList};
    altFValueList[groupFeat] = newFeatValue;
    return {altFValueList, newFeatValue }
}


//Saves via handleSave and creates the alternative scenario in case of grouped features
const handleCreateAlt = (e)=>{
    if(fieldsComplete()){
    let seqNr;
    handleAddScenario(scNr)
    .then((nr)=>{seqNr=nr;
        //here handleSave
        const chosenList = [...chosen];
        const fValueList = chosen[0]["data"]["fValues"];
        const {altFValueList, newFeatValue} = getAltFValues(fValueList);
        chosenList[0]["data"]["altnr"]= seqNr;
        setChosen(chosenList);
        setNewScNr(seqNr);
        setScNr(seqNr);
        handleSave(e)
        .then(()=>{
        //load data in forms
        setChosen([{}]);
        pemFirestore.collection('users').doc(uid).collection('DceList').doc(dceId).collection('Scenarios').doc(seqNr.toString()).get()
    .then((doc)=>{
        let data = doc.data();
        let newChosenList = [data]
        newChosenList[0]["data"]["fValues"]=altFValueList;
        setChosen(newChosenList);
        let response = pemFirestore.collection('users').doc(uid).collection('DceList').doc(dceId).collection('Scenarios');
        response.doc(seqNr.toString()).set(newChosenList[0]);
     })
    })
    }).catch((err)=>{
        console.log('Error with saving scenario'+err);
    })
}
}

//Checks if all required field have an entry
const fieldsComplete = ()=>{
    let textOrImageComplete = false;
    let featuresComplete = false;
    if (chosen[0]["data"]["scText"].length!==0 || chosen[0]["data"]["scImg"].length!==0){
        textOrImageComplete=true}
    else{
    alert("Fill in the Scenario Text or upload a Scenario Image, or do both") 
    }
    
    featuresComplete= !dceFeatures.some(feature=>{
        console.log('check features complete: '+JSON.stringify(chosen[0]["data"]))
        console.log('feature: '+JSON.stringify(feature))
        if(chosen[0]["data"]["fValues"][feature.featureName].length===0){
            alert("Select a value for every feature in the scenario");
             
        } return (chosen[0]["data"]["fValues"][feature.featureName].length===0);
    })
    console.log('text and feat complete: '+textOrImageComplete+featuresComplete)
return textOrImageComplete&&featuresComplete;    
}

 //Checks if all required field have an entry and saves to Firestore if ok.
 //Else alert with error message
const handleSave = async (e)=>{
e.preventDefault();

if(fieldsComplete()){
let response = pemFirestore.collection('users').doc(uid).collection('DceList').doc(dceId).collection('Scenarios');
response.doc(scNr.toString()).set(chosen[0]);
const fValues = chosen[0]["data"]["fValues"];
const altnr = chosen[0]["data"]["altnr"];
const {altFValueList} = getAltFValues(fValues);
if (altnr){
    let response = pemFirestore.collection('users').doc(uid).collection('DceList').doc(dceId).collection('Scenarios');
        response.doc(altnr.toString()).update({
            "data.fValues": altFValueList
        });
}
setIsSaved(true);
}
}

const {scenarioHtml} = useNewScenarioHtml(dceDetails, chosen, handleChosen, handleSave, handleEntry, handleCreateAlt, dceFeatures, dceId, scNr, setChosen, setIsSaved, isSaved)
//Html to show in browser
    return (
        <div>
                    {scenarioHtml}

              {/* {dceDetails&&dceDetails.details.grouped&&chosen[0]["data"]&&<h5>Paired with scenario nr: {chosen[0]["data"]["altnr"]}</h5>}
              <form className="form-row" > 
              <h6>Features</h6>
        {chosen[0]["data"]&& dceFeatures.map((feature,i) =>{
                  return (
                  <label htmlFor={i} className="col-md-3 col-form-label">{feature.featureName}
                  <select className="form-select form-select-sm col-md-3  mb-3" disabled={chosen[0]["data"]["altSc"]} name={feature.featureName} defaultValue={chosen[0]["data"]["fValues"][feature.featureName]} onChange={handleChosen} id={i} required>
                   <option value="" disabled></option>
                   <option value={feature.featureValue1}>{feature.featureValue1}</option>
                   <option value={feature.featureValue2} >{feature.featureValue2}</option>
                   </select>
                   </label>
                  )
              })}
              </form>
              {chosen[0]["data"]&&<form id="textForm" onSubmit={handleSave}>
              <div>
              <h6>Scenario text</h6>
              <textarea className="form-control form-control-sm mb-3" name="scText" value={chosen[0]["data"]["scText"]} placeholder="Enter scenario text" id="scText" onChange={e=>handleEntry(e)}></textarea>
               </div>
                                     
               <div>
                   <h6>Scenario explanation</h6>
                   <textarea className="form-control form-control-sm mb-3" type="text" name="scExpl" value={chosen[0]["data"]["scExpl"]} placeholder="Enter additional explanatory text" id="scExpl" onChange={e=>handleEntry(e)}></textarea>

               </div>
              
               <h6>Scenario image link</h6>
               <UploadForm dceId={dceId} scNr={scNr} setUrl={setUrl} />
               {chosen[0]["data"]["scImg"]&&<img src={chosen[0]["data"]["scImg"]} className='scImage' alt="scenario image"/>}
               {dceDetails&&dceDetails.details.grouped&&!chosen[0]["data"]["altnr"]&&<input className="btn  btn-outline-success mb-3" type="button" onClick={handleCreateAlt} value="Save & Create pair"></input>}
               {(chosen[0]["data"]["altnr"]||!dceDetails.details.grouped)&&<input className="btn  btn-outline-success mb-3" type="submit" value="Save"></input>}
               </form>}
               {isSaved&&<div className={isSaved}>Scenario has been saved</div>} */}
    </div>
    )
}
