import React, { useEffect, useState} from 'react';
import { useLocation} from "react-router-dom";
import useFirestoreLoadResult from '../../firebase/useFirestoreLoadResults';
import useSetAspectObjs from './useSetAspectObjs';
import useGetResultHtml from './useGetResultHtml';


export default function ResultDce({dceId, dceNameEdit, dceDetails, dceFeatures, userId}) {
    // const {userId, dceId } = useParams();
    const [finalHtml, setFinalHtml] = useState([]);
    // const [resultHtml, setResultHtml] = useState([]);
    const [aspects, setAspects] = useState([]);
    // const [aspectObjs, setAspectObjs] = useState([]);
    const location = useLocation();
    // const user = useContext(UserContext);
    console.log('user and dce id in ResultDce: '+JSON.stringify(userId) +' '+dceId)
    console.log('dceDetails in ResultDce start: '+JSON.stringify(dceDetails))
    const {results} = useFirestoreLoadResult(userId ,dceId);
    const [ind, setInd] = useState(null);
    const {details} = dceDetails;



const {aspectObjs} = useSetAspectObjs(userId, dceId, details, dceFeatures);
const {resultHtml} = useGetResultHtml(aspectObjs, results);

useEffect(() => {
    if(aspectObjs.length===0){
        setFinalHtml([<div><h5>No aspects defined.</h5></div>])
    }else 
setFinalHtml(resultHtml)
    return 

}, [aspectObjs,results])


    return (
        <div>
           <div className="mb-5"><h3>Total results from all particpants: </h3>
            {resultHtml}</div>
           
        </div>
    )
}
